@import "../../../data/styles.css";

.education-body {
	margin-bottom: 0px;

}

.education {
	display: flex;
	padding-bottom: 40px;
	width: 400px;
}

.education-image {
	height: 30px;
	width: 30px;
	border-radius: 50%;
	outline: 6px solid white;
	box-shadow: 0px 7px 10px rgba(0, 0, 0, 0.25);
}

.education-title {
	font-size: 15px;
	font-weight: 700;
	padding-left: 20px;
	margin-top: -3px;
	color: var(--secondary-color);
}

.education-subtitle {
	position: absolute;
	font-size: 12px;
	color: var(--secondary-color);
	padding-top: 22px;
	padding-left: 50px;
}

.education-duration {
	position: absolute;
	width: 350px;
	font-size: 12px;
	padding-top: 20px;
	text-align: right;
	color: var(--tertiary-color);
}

/* Mobile Styles */
@media (max-width: 600px) {
	.education-body {
		margin-bottom: 0px;
	
	}
	
	.education {
		display: flex;
		padding-bottom: 40px;
		width: 300px;
	}
	
	.education-image {
		height: 30px;
		width: 30px;
		border-radius: 50%;
		outline: 6px solid white;
		box-shadow: 0px 7px 10px rgba(0, 0, 0, 0.25);
	}
	
	.education-title {
		font-size: 15px;
		font-weight: 700;
		padding-left: 20px;
		margin-top: -3px;
		color: var(--secondary-color);
	}
	
	.education-subtitle {
		position: absolute;
		font-size: 12px;
		color: var(--secondary-color);
		padding-top: 22px;
		padding-left: 50px;
	}
	
	.education-duration {
		position: absolute;
		width: 350px;
		font-size: 12px;
		padding-top: 20px;
		text-align: right;
		color: var(--tertiary-color);
	}
}